import $ from "../$.js";

import {hideElement, initQueryAndApply} from "../utils/dom.js";
import {profile} from "../introspection/profile.js";
import {raceWinner} from "../introspection/race.js";

let {MutationObserver} = $(window);

const {ELEMENT_NODE} = Node;

/**
 * Hide a specific element through a XPath 1.0 query string.
 * See {@tutorial xpath-filters} to know more.
 * @alias module:content/snippets.hide-if-matches-xpath
 *
 * @param {string} query The XPath query that targets the element to hide.
 *
 * @since Adblock Plus 3.9.0
 */
export function hideIfMatchesXPath(query) {
  let {mark, end} = profile("hide-if-matches-xpath");

  let queryAndApply = initQueryAndApply(`xpath(${query})`);

  let callback = () => {
    mark();

    queryAndApply(node => {
      win();
      if ($(node).nodeType === ELEMENT_NODE)
        hideElement(node);
      else
        $(node).textContent = "";
    });

    end();
  };

  let mo = new MutationObserver(callback);
  let win = raceWinner(
    "hide-if-matches-xpath",
    () => mo.disconnect()
  );
  mo.observe(document, {characterData: true, childList: true, subtree: true});
  callback();
}
